import config from './config.js'
import ajax from './api.js'
/*
 所有用户的api
 */
class UserService {
  async getResponse (type, data) {
    let response
    let apiType = config[type] || {url: '', method: 'POST'}
    try {
      response = await ajax(apiType.url, data, apiType.method, apiType.baseUrl)
    } catch (e) {
      console.log(e)
    }
    return response
  }
  async getResponseByBody (type, currentPage, pageSize, data) {
    let response
    let apiType = config[type]
    let url = `${apiType.url}?currentPage=${currentPage}&pageSize=${pageSize}`
    try {
      response = await ajax(url, data, apiType.method)
    } catch (e) {
      console.log(e)
    }
    return response
  }
}
export default new UserService()



