<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content" v-if="isList">
        <el-table :data="apiList" border>
          <el-table-column label="API名称" prop="apiChName">
          </el-table-column>
          <el-table-column label="API版本" prop="version">
          </el-table-column>
          <el-table-column label="所属分类" prop="appName">
          </el-table-column>
          <el-table-column label="clientId" width="200">
            <template scope="scope">
              <template v-if="scope.row.checkState == constans['STATE-PASS']">{{scope.row.clientId}}</template>
              <template v-else>——</template>
            </template>
          </el-table-column>
          <el-table-column label="secret" width="200">
            <template scope="scope">
              <template v-if="scope.row.checkState == constans['STATE-PASS']">{{scope.row.secret}}</template>
              <template v-else>——</template>
            </template>
          </el-table-column>
          <!--<el-table-column label="密钥" prop="secretKey"></el-table-column>-->
          <!--<el-table-column label="频次" :formatter="formatterRate"></el-table-column>
          <el-table-column label="最大配额" prop="quotaMax"></el-table-column>
          <el-table-column label="配额重复周期" prop="quotaRenewalRate"></el-table-column>-->
          <el-table-column label="状态">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkMem && scope.row.checkState*1 == constans['STATE-NOPASS']">
                <p>{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <router-link :to="{name: 'developApiDetail', query: {'apiid': scope.row.apiId}}" class="open-list-btn">查看</router-link>
              <a @click="delKey(scope.row.id, scope.$index)" class="open-list-btn">删除</a>
              <!--<router-link class="open-list-btn" size="small" :to="{name: 'dataAnalysis', query: {apiId: scope.row.id}}" v-if="scope.row.checkState==constans['STATE-PASS']">数据分析</router-link>-->
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page"></el-pagination>
      </section>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontApiDetail from '@/views/apiDetail.vue'
  import userService from '@/api/userService.js'
  export default{
    name: 'api-list-common',
    data () {
      return {
        applyId: this.$route.query.applyId,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0,
        isList: true,
        currentPage: 1,
        pageSize: 10,
        apiList: [],
        total: 0,
        constans: constans,
        checkState: config['checkState']
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize, this.applyId)
    },
    methods: {
      formatterRate (row) {
        return row.rate || 1 + '次/秒'
      },
      //获取api列表
      fetch (currentPage, pageSize, id) {
        userService.getResponse('useApiList', {currentPage, pageSize, id})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.apiList = oData.data.auditList
              this.total = oData.data.authIds.length
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize, this.applyId)
      },
      // 详情页
      openDetail (id, name) {
        this.apiid = id
        this.isList = false
        this.$route.meta.curmbsData.push({
          title: `${name}详情`,
          path: ''
        })
      },
      delKey (auditId, index) {
        this.$confirm('不在使用该接口?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          userService.getResponse('delKey', {auditId})
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status === 'success') {
                this.$message.success('删除成功')
                this.apiList.splice(index, 1)
              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      }
    },
    components: { compontLeft, compontApiDetail }
  }
</script>



