<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <el-collapse v-model="activeName" class="open-wrap-content">
        <h1 class="open-api-title">{{ apichname }}</h1>
        <p class="open-api-detail">{{ desc }}</p>
        <el-collapse-item :title="item.title" :name="index" v-for="(item, index) in ruleForm">
          <p v-if="item.type">{{item.data}}</p>
          <!--<el-input type="textarea" :rows="2" v-model="item.data" v-if="item.type">
          </el-input>-->
          <!--<pre class="brush: js" v-if="item.type">{{item.data}}</pre>
          <code v-if="item.type">{{item.data}}</code>-->
          <el-table :data="item.data" border class="open-api-detail" v-else>
            <el-table-column :prop="key" :label="val" v-for="(val, key) in item.head"></el-table-column>
          </el-table>
        </el-collapse-item>
      </el-collapse>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import config from '@/assets/js/config.js'
  import constans from '@/assets/js/constans.js'
  import compontLeft from '@/components/LEFT.vue'

  export default{
    name: 'api-detail',
    data () {
      return {
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0,
        apiid: this.$route.query.apiid,
        states: config.openState,
        apichname: '',
        desc: '',
        appid: '',
        ruleForm: [
        ],
        activeName: ['1'],
        // groups: [],
        argTypes: config.argTypes,
        requireds: config.requireds,
        httpmds: config.httpmds,
        openState: config.openState,
        userInfo: this.$store.state.login.userInfo,
        gwRate: config.gwRate // 网关限频
      }
    },
    created () {
      // 获取api详情
      this.$ajax(config.url.showapidetail, {apiid: this.apiid}, config.method.showapidetail)
        .then((response) => {
          let oData = response.data
          let message = oData.message
          let nCode = oData.code
          if (nCode === '1') {
          let apiDetail = oData.data
          this.formatData(apiDetail)
        } else {
            this.$message.error(message)
            this.$router.go(-1)
        }
      })
      .catch((error) => {
        console.log(error)
        this.$router.go(-1)
      })
      // 获取功能组
      /* this.$ajax(config.url.groupList, {categoryId: this.categoryId}, config.method.groupList)
        .then((response) => {
        let oData = response.data
        let message = oData.message
        let nCode = response.code
        if (nCode === '1') {
        let dataList = oData.data
        let allData = dataList.items
        allData.forEach((item) => {
          this.groups.push({
          id: item.id,
          name: item.appname
        })
      })
      } else {
          this.$message.error(message)
      }
    })
    .catch(error => {
      this.$message.error(error)
      }) */
    },
    methods: {
      formatData (data) {
        this.apichname = data.apichname
        this.desc = data.desc
        this.appid = data.appid
        let gatewayUrls = data.gatewayUrls[0]
        let appname = data.appname
        let httpmethod = data.httpmethod
        let testendpoint = data.testendpoint
        let endpoint = data.endpoint
        let state = data.state
        let headers = data.headers
        let args = data.args
        let result = data.result
        let codes = data.errcodes
        let exname = data.exname
        let extype = data.extype
        let excode = data.statecode
        let exvalue = data.retexample.exvalue
        let per = data.per
        let rate = data.rate
        let quota_max = data.quota_max
        let quota_renewal_rate = data.quota_renewal_rate
        let version = data.apiversion.version
        let versionRemark = data.apiversion.versionRemark
        let apiDetail = [
          {
            title: '公共参数',
            head:{
              name: '参数名称',
              value: '参数值'
            },
            data: [{
              'name': 'Http请求地址',
              'value': gatewayUrls
            }, {
              'name': 'Http方法',
              'value': httpmethod
            }, {
              'name': '服务分类',
              'value': appname
            }, {
              'name': '正式URL',
              'value': testendpoint
            }/* , {
             'name': '正式URL',
             'value': endpoint
             } */, {
              'name': '状态',
              'value': config['openState'][state]
            }, {
              'name': '频次',
              'value': rate + '次/秒'
            }/*, {
              'name': '频次周期',
              'value': rate + 's'
            }*/, {
              'name': '最大配额',
              'value': quota_max + '次'
            }, {
              'name': '配额重置周期',
              'value': this.gwRate.quotaRenewalRate[quota_renewal_rate]
            }, {
              'name': '版本号',
              'value': version
            }, {
              'name': '版本描述',
              'value': versionRemark
            }]
          }, {
            title: 'Header参数',
            head:{
              argname: '参数名称',
              argtype: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(headers)
          }, {
            title: '应用参数',
            head:{
              argname: '参数名称',
              argtype: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(args)
          }, {
            title: '返回类型',
            head:{
              retname: '参数名称',
              rettype: '参数选项',
              desc: '示例值',
              example: '描述'
            },
            data: result
          }, {
            title: '返回示例',
            type: 'textarea',
            data: exvalue
          }, {
            title: '错误代码',
            head:{
              errname: '错误码',
              desc: '描述'
            },
            data: codes
          },/* {
            title: '频次限制',
            head:{
              name: '名称',
              value: '值'
            },
            data: [{
              'name': '频次',
              'value': per + '次'
            }, {
              'name': '频次周期',
              'value': rate + 's'
            }, {
              'name': '最大配额',
              'value': quota_max
            }, {
              'name': '配额重置周期',
              'value': quota_renewal_rate
            }]
          } */
        ]
        constans['SUPPLY-USER'] == this.userInfo.checkState && apiDetail[0].data.slice(3,1)
        /*apiDetail.push({
          title: '版本信息',
          head:{
            name: '参数名称',
            value: '参数值'
          },
          data: [{
            'name': '版本号',
            'value': version
          }, {
            'name': '版本描述',
            'value': versionRemark
          }]
        })*/
        this.ruleForm.push(...apiDetail)
      },
      convertData (data) {
        data.forEach(function (item) {
          let required = item.required
          item.required = config['requireds'][required]
        })
        return data
      }
    },
    components: { compontLeft }
  }
</script>



