const getPageData = (pageSize, pageIndex, dataList) => {
  let endIndex = pageSize * pageIndex
  let startIndex = endIndex - pageSize
  return dataList.slice(startIndex < 0 ? 0 : startIndex, endIndex)
}
export {getPageData}



