<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <el-table :data="apiList" border>
        <el-table-column label="API名称" prop="apichname"></el-table-column>
        <el-table-column label="版本号" prop="apiversion.version"></el-table-column>
        <el-table-column label="状态">
          <template scope="scope">
            <el-popover trigger="hover" placement="top" v-if="scope.row.checkState*1 == constans['STATE-NOPASS']">
              <p v-if="scope.row.checkMem">{{ scope.row.checkMem }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
              </div>
            </el-popover>
            <p v-else>
              {{checkState[scope.row.checkState]}}
            </p>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
            <template  v-if="scope.row.checkState==constans['STATE-UNCOMMITTED'] && scope.row.userid == userId">
              <a class="open-list-btn" size="small" @click="submitCheck(scope.$index)">提交审核</a>
              <router-link class="open-list-btn" :to="{name: 'addApi', query: {apiid: scope.row.id}}">编辑</router-link>
              <a class="open-list-btn" @click="deleteApi(scope.$index, scope.row.id)">删除</a>
            </template>
            <template  v-if="scope.row.checkState==constans['STATE-NOPASS'] && scope.row.userid == userId">
              <a class="open-list-btn" size="small" @click="submitCheck(scope.$index)">提交审核</a>
              <router-link class="open-list-btn" :to="{name: 'addApi', query: {apiid: scope.row.id}}">编辑</router-link>
            </template>
<router-link class="open-list-btn" size="small" :to="{name: 'apiAnalysis', query: {apiId: scope.row.id}}" v-if="scope.row.checkState==constans['STATE-PASS']">数据分析</router-link>
            <template v-if="scope.row.checkState==constans['STATE-PASS'] && scope.row.apiversion && scope.row.apiversion.newVersion && scope.row.userid == userId">
              <router-link class="open-list-btn" size="small" :to="{name: 'addApi', query: {apiid: scope.row.id, version: 1}}">更新版本</router-link>
            </template>
          <router-link class="open-list-btn" :to="{name: 'apiDetail', query: {apiid: scope.row.id}}">查看</router-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page"></el-pagination>
    </section>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontCurmbs from '@/components/CURMBS.vue'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  import {getPageData} from '@/assets/js/common.js'
  export default{
    name: 'api-list',
    data () {
      return {
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0,
        currentPage: 1,
        pageSize: 10,
        apiList: [],
        total: 0,
        appid: this.$route.query.appid,
        checkState: config.checkState,
        constans: constans, // 常量
        userId: this.$store.state.login.userInfo.id
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize, this.appid, this.userId)
    },
    methods: {
      //获取列表
      fetch (currentPage, pageSize, appid, userid) {
        this.$ajax(config.url.apiList, {currentPage, pageSize, appid, userid}, config.method.apiList)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.apiList = JSON.parse(oData.data).items
              this.total = JSON.parse(oData.data).totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },

      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize, this.appid, this.userId)
      },
      // 删除
      deleteApi (index, apiid) {
        this.$confirm('此操作将永久删除该API, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$ajax(config.url.delApi, {apiid}, config.method.delApi)
              .then((response) => {
                let oData = response.data
                let message = oData.message
                let nCode = oData.code
                if (nCode === '1') {
                  this.apiList.splice(index, 1)
                  this.total--
                } else {
                  this.$message.error(message)
                }
              })
              .catch(error => {
                this.$message.error(error)
              })
          })
          .catch()
      },

      // 提交审核
      submitCheck (nIndex) {
        let targetData = this.apiList[nIndex]
        let apiid = targetData.id
        this.$ajax(config.url.apiVerify, {apiid}, config.method.apiVerify)
          .then(response => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.$message.success('提交审核成功')
              targetData.checkState = 1
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: { compontLeft }
  }
</script>



