<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <p class="open-person-info" v-for="userInfo in userInfos"><span class="open-person-name">{{ userInfo.name }}</span>{{ userInfo.value }}</p>
    </section>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constants from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  export default{
    name: 'person-center',
    data () {
      return {
        navData: config.navLeft['person'],
        targetName: 0
      }
    },
    created () {
      this.curmbsData = [{
        title: '控制台',
        path: ''
      }, {
        title: '个人中心',
        path: ''
      }];
      const userInfo = this.$store.state.login.userInfo
      this.userType = userInfo.userType
      this.userInfos = [
        {
          'name': '用户名：',
          'value': userInfo.username
        }, {
          'name': '邮箱：',
          'value': userInfo.email
        }/*, {
          'name': '手机：',
          'value': userInfo.tel
        }*/, {
          'name': '角色：',
          'value': config.userType[this.userType]
        }/*, {
          'name': '备注：',
          'value': userInfo.mem
        }*/
      ]
      /* userInfo.userType == 1 && this.userInfos.push(...[{
        'name': 'APPKEY：',
        'value': userInfo.id
      }, {
        'name': 'APPSECRET：',
        'value': userInfo.appSecret
      }]) */
    },
    methods: {

    },
    components: { compontLeft }
  }
</script>



