<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <h1 class="open-api-title">{{ navData.data[targetName].name }}</h1>
      <template v-for="info in guideInfo">
        <h3 class="open-doc-title" v-if="info.title">{{ info.title }}</h3>
        <p class="open-doc-info" v-html="info.desc"></p>
        <p class="open-doc-info" v-if="info.data" >
          <el-table border :data="info.data.data">
            <template v-for="(value, key) in info.data.header">
              <el-table-column :label="value" :prop="key"></el-table-column>
            </template>
          </el-table>
        </p>
      </template>
    </section>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import guide from '@/assets/js/guide.js'
  export default{
    name: 'guide',
    data () {
      return {
        navData: config.navLeft.doc,
        targetName: this.$route.meta.index,
        guideInfo: guide[this.$route.name]
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      this.fetchData()
    },
    methods: {
      //刷新路由
      fetchData () {
        let routeName = this.$route.name
        this.targetName = this.$route.meta.index
        this.guideInfo = guide[routeName]
      }
    },
    components: { compontLeft }
  }
</script>



