import Vue from 'vue'
import Router from 'vue-router'
import header from '@/components/HEAD.vue' // 首页
import compontCurmbs from '@/components/CURMBS.vue'
import footer from '@/components/FOOTER.vue'
import index from '../views/index.vue' // 首页
import login from '../views/login.vue' // 登录
import signUp from '../views/signUp.vue' // 注册
import signSuccess from '../views/signSuccess.vue' // 注册
import addApi from '@/views/apiSupply/addApi.vue'
import apiDetail from '@/views/apiSupply/apiDetail.vue'
import groupList from '@/views/apiSupply/groupList.vue'
import apiList from '@/views/apiSupply/apiList.vue'
import apiListCommon from '@/views/apiList.vue'
import applyList from '@/views/developer/applyList.vue'
import newGuide from '@/components/GUIDE.vue' // 帮助中心

// import apiDetailCommon from '@/views/apiDetail.vue'
import person from '@/views/person.vue'
import useApiList from '@/views/developer/useApiList.vue'
import useMyApi from '@/views/apiSupply/useMyApi.vue'
import dataAnalysis from '@/views/dataAnalysis.vue'

Vue.use(Router)

let router = new Router({
  // base: '../',
  // mode: 'history',
  suppressTransitionError: process.env.NODE_ENV.trim() === 'production', // 生产环境下不抛出异常
  routes: [
    {
      path: '*',
      redirect: {
        name: 'index'
      }
    },
    {
      path: '/',
      redirect: {
        name: 'index'
      }
    }, {// 首页
      path: '/index/',
      name: 'index',
      components: {
        header: header,
        default: index,
        footer: footer
      },
      meta: {
        title: '首页'
      }
    }, {// 登录
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录'
      }
    }, {// 注册
      path: '/signup',
      name: 'signUp',
      component: signUp,
      meta: {
        title: '注册'
      }
    }, {// 注册成功
      path: '/signSuccess',
      name: 'signSuccess',
      component: signSuccess,
      meta: {
        title: '注册成功'
      }
    }, {
      path: '/apiList',
      name: 'apiListCommon',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiListCommon,
        footer: footer
      },
      meta: {
        title: 'api列表',
        curmbsData: [{
          title: 'api列表',
          path: '/apiList'
        }]
      }
    }, {
      path: '/supply/person',
      name: 'supplyPerson',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: person,
        footer: footer
      },
      meta: {
        title: '个人中心',
        curmbsData: [{
          title: '个人中心',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // 上传接口
      path: '/supply/addApi',
      name: 'addApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: addApi,
        footer: footer
      },
      meta: {
        title: '上传接口',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '服务分类',
          path: '/supply/groupList'
        }, {
          title: 'API上传',
          path: '/supply/addApi'
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // api详情
      path: '/supply/apiDetail',
      name: 'apiDetail',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiDetail,
        footer: footer
      },
      meta: {
        title: '详情',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '服务分类',
          path: '/supply/groupList'
        }, {
          title: 'API详情',
          path: '/supply/apiDetail'
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // 服务分类
      path: '/supply/groupList',
      name: 'groupList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: groupList,
        footer: footer
      },
      meta: {
        title: '服务分类',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '服务分类',
          path: '/supply/groupList'
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // api列表
      path: '/supply/apiList',
      name: 'apiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiList,
        footer: footer
      },
      meta: {
        title: 'api列表',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '服务分类',
          path: '/supply/groupList'
        }, {
          title: 'api列表',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // api数据分析
      path: '/supply/dataAnalysis',
      name: 'apiAnalysis',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: dataAnalysis,
        footer: footer
      },
      meta: {
        title: 'api列表',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '服务分类',
          path: '/supply/groupList'
        }, {
          title: 'API数据分析',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, {// 个人中心
      path: '/develop/person',
      name: 'developPerson',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: person,
        footer: footer
      },
      meta: {
        title: '个人中心',
        curmbsData: [{
          title: '个人中心',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // 使用者api的用户管理
      path: '/develop/useMyApi',
      name: 'useMyApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: useMyApi,
        footer: footer
      },
      meta: {
        title: '使用者管理',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '使用者管理',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, {// 使用者使用的应用列表
      path: '/develop/applyList',
      name: 'applyList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: applyList,
        footer: footer
      },
      meta: {
        title: '应用管理',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '应用管理',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, {// 使用者使用的api列表
      path: '/develop/useApiList',
      name: 'useApiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: useApiList,
        footer: footer
      },
      meta: {
        title: '使用的API',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '应用管理',
          path: '/develop/applyList'
        }, {
          title: '使用的API',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, {
      path: '/develop/apiList',
      name: 'applyApiList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiListCommon,
        footer: footer
      },
      meta: {
        title: 'api列表',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '应用管理',
          path: '/develop/applyList'
        }, {
          title: '申请API',
          path: ''
        }]
      }
    }, { // api详情
      path: '/develop/apiDetail',
      name: 'developApiDetail',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiDetail,
        footer: footer
      },
      meta: {
        title: '详情',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '应用管理',
          path: '/develop/applyList'
        }, {
          title: 'API详情',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // api数据分析
      path: '/develop/dataAnalysis',
      name: 'dataAnalysis',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: dataAnalysis,
        footer: footer
      },
      meta: {
        title: 'API数据分析',
        curmbsData: [{
          title: '控制台',
          path: ''
        }, {
          title: '使用者管理',
          path: '/develop/useMyApi'
        }, {
          title: 'API数据分析',
          path: ''
        }],
        needToLogin: true // 用于权限拦截
      }
    }, { // 帮助中心--新手指南
      path: '/doc/newGuide',
      name: 'newGuide',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '新手指南',
        index: 0, // 导航的顺序
        curmbsData: [{
          title: '帮助中心',
          path: ''
        }, {
          title: '新手指南',
          path: ''
        }]
      }
    }, { // 帮助中心--服务注册
      path: '/doc/regService',
      name: 'regService',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: 'API提供者服务注册',
        index: 1, // 导航的顺序
        curmbsData: [{
          title: '帮助中心',
          path: ''
        }, {
          title: 'API提供者服务注册',
          path: ''
        }]
      }
    }, { // 帮助中心--API调用方法详解
      path: '/doc/useApi',
      name: 'useApi',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: 'API调用方法详解',
        index: 2, // 导航的顺序
        curmbsData: [{
          title: '帮助中心',
          path: ''
        }, {
          title: 'API调用方法详解',
          path: ''
        }]
      }
    }, { // 帮助中心--API调用方法详解
      path: '/doc/definition',
      name: 'definition',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '名词解释',
        index: 3, // 导航的顺序
        curmbsData: [{
          title: '帮助中心',
          path: ''
        }, {
          title: '名词解释',
          path: ''
        }]
      }
    }, { // 帮助中心--API调用方法详解
      path: '/doc/apiStandard',
      name: 'apiStandard',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: newGuide,
        footer: footer
      },
      meta: {
        title: '接口规范',
        index: 4, // 导航的顺序
        curmbsData: [{
          title: '帮助中心',
          path: ''
        }, {
          title: '接口规范',
          path: ''
        }]
      }
    }
  ]
})
import * as types from '@/store/mutation-types.js'
import config from '@/assets/js/config.js'
// 权限拦截
router.beforeEach((to, from, next) => {
  router.app.curmbsData = to.meta.curmbsData
  let userInfo = localStorage.getItem('session')
  let oUserInfo
  if (userInfo) {
    userInfo = JSON.parse(userInfo)
    let __time = userInfo.time * 1
    oUserInfo = userInfo.userInfo
    let sNowTime = new Date().getTime() * 1
    let expiredTime = config.expiredTime // 过期时间
    oUserInfo = sNowTime - __time < expiredTime ? oUserInfo : null
    router.app.$options.store.commit(types.UPDATEUSERINFO, oUserInfo)
  }
  if (!router.app.userInfo && !userInfo && !oUserInfo && to.meta.needToLogin) { // 没有用户数据且页面有权限则跳到首页
    return next({name: 'index'})
  }
  next()
})

export default router



