import ajax from './ajax.js'

export default function api (url, data, method = 'GET', baseUrl) {
  switch (method) {
    case 'POST':
      return ajax.post(url, data, baseUrl)
    case 'GET':
      for (let key in data) {
        url.indexOf('?') > 0 ? url += '&' + key + '=' + data[key] : url += '?' + key + '=' + data[key]
      }
      return ajax.get(url)
    case 'POSTJSON':
      return ajax.postJson(url, data)
    case 'DELETE':
      for (let key in data) {
        url.indexOf('?') > 0 ? url += '&' + key + '=' + data[key] : url += '?' + key + '=' + data[key]
      }
      return ajax.delete(url)
    case 'PUT':
      return ajax.put(url, data)
  }
}



