<template>
  <section class="open-login-bg">
    <div class="open-login-wrap">
      <div class="open-login-img"></div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <h3 class="open-content-title">注册</h3>
        <el-form-item label="用户名" prop="username" class="open-form-input">
          <el-input v-model="ruleForm.username" placeholder="请输入用户名称"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email" class="open-form-input">
          <el-input type="email" v-model="ruleForm.email" placeholder="请输入用户邮箱"></el-input>
        </el-form-item>
        <!--<el-form-item label="手机" prop="tel" class="open-form-input">
          <el-input type="tel" v-model="ruleForm.tel" placeholder="请输入手机号码"></el-input>
        </el-form-item>-->
        <el-form-item label="密码" prop="password" class="open-form-input">
          <el-input type="password" v-model="ruleForm.password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="rePassword" class="open-form-input">
          <el-input type="password" v-model="ruleForm.rePassword" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item label="角色" prop="userType">
          <el-radio-group v-model="ruleForm.userType" @keyup.enter.native="onEnter(ruleForm)">
            <el-radio class="radio" :label="key" v-for="(item, key) in userType" v-if="key != 0">{{item}}</el-radio>
          </el-radio-group>
          <!--<el-select v-model="ruleForm.userType">
            <el-option :label="item" :value="key" v-if="key != 0" v-for="(item, key) in userType"></el-option>
          </el-select>-->
        </el-form-item>
        <!--<el-form-item label="备注" prop="mem" class="open-form-input">
          <el-input type="textarea" :rows="2" v-model="ruleForm.mem" placeholder="请输入备注"></el-input>
        </el-form-item>-->
        <el-form-item class="open-center">
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" autofocus>注册</el-button>
        </el-form-item>
        <p class="open-message">已有账号，直接<router-link :to="{name: 'login'}">登录</router-link></p>
      </el-form>
    </div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import md5 from 'js-md5'
  import userService from '@/api/userService.js'
  export default{
    name: 'sign-up',
    data(){
      let checkName = (rule, value, callback) => {
        userService.getResponse('checkName', {username: value})
          .then(response => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status != 'success') {
              callback(new Error(message));
            } else {
              callback()
            }
          })
          .catch((error) => {
            callback(new Error('接口错误，请联系管理员'));
          })
      }
      let validatorEmail = (rule, value, callback) => {
        userService.getResponse('checkEmail', {email: value})
          .then(response => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status != 'success') {
              callback(new Error(message));
            } else {
              callback()
            }
          })
          .catch((error) => {
            callback(new Error('接口错误，请联系管理员'));
          })
      }
      let validatorPwd = (ruke, value, callback) => {
        value != this.ruleForm.password ? callback(new Error('两次输入密码不同')) : callback();
      }
      return {
        userType: config.userType,
        ruleForm: {
          username: '',
          email: '',
          tel: '',
          password: '',
          userType: '',
          rePassword: '',
          mem: ''
        },
        rules: {
          username: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur',
          }, {
            min: 3,
            max: 20,
            message: '长度在3到20个字符之间'
          }, {
            message: '用户名只能包括数字、英文、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.userName
          }, {
            validator: checkName,
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          } , {
            min: 6,
            max: 20,
            message: '长度在6到20个字符之间'
          }, {
            message: '密码只能包括数字、字母、.、-、*、@、?、!',
            trigger: 'change, blur',
            pattern: config.validate.passWord
          }],
          rePassword: [{
            required: true,
            message: '请再次输入密码',
            trigger: 'blur'
          }, {
            trigger: 'blur',
            validator: validatorPwd
          }],
          email: [{
            required: true,
            message: '请输入用户邮箱',
            trigger: 'blur'
          }, {
            message: '请输入正确的邮箱格式',
            trigger: 'change, blur',
            pattern: config.validate.email
          }, {
            validator: validatorEmail,
            trigger: 'blur'
          }],
          tel: [{
            // required: true,
            message: '请输入用户电话',
            trigger: 'blur',
            max: 11
          }, {
            message: '请输入正确的电话号码',
            trigger: 'change, blur',
            pattern: config.validate.tel
          }],
          userType: [{
            required: true,
            trigger: 'blur',
            message: '请选择用户类型'
          }]
        }
      }
    },
    methods: {
      // 点击登录
      submitForm (ruleForm) {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.onRegist()
          } else {
            this.$message.error('注册失败!!');
            return false
          }
        })
      },
      // 注册
      onRegist() {
        // this.ruleForm.password = md5(this.ruleForm.password);
        let ruleForm = {...this.ruleForm}
        ruleForm.password = md5(ruleForm.password);
        delete  ruleForm.rePassword
        userService.getResponse('register', ruleForm).then(response => {
          let oData = response.data
          let sMessage = oData.message;
          let nCode = oData.code;
          if (nCode === '1') {
            // this.$message.success('注册成功, 请等待管理员审核，邮件会发送至您的邮箱')
             this.$router.push({
              name: 'signSuccess'
            })
          }else{
            this.$message.error(sMessage);
          }
        })
      },
      // enter键
      onEnter() {
        this.$nextTick(function() {
          this.submitForm ()
        })
      }
    },
    components: { }
  }
</script>



