<template>
  <section class="open-login-bg">
    <div class="open-login-info"><span class="open-content-title">注册成功</span><br>审核结果将在5分钟内会以邮件形式通知您。<br><span class="goto">{{jumpTime}}</span>秒后回到首页，<router-link :to="{name: 'index'}">点此手动跳转</router-link></div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import md5 from 'js-md5'
  export default{
    name: 'sign-success',
    data(){
      return {
        jumpTime: config.jumpTime
      }
    },
    beforeRouteLeave (to, from, next) {
      clearTimeout(this.time)
      this.jumpTime = 5
      next()
    },
    created () {
      this.time = setInterval(() => {
        this.jumpTime --;
        if (this.jumpTime <= 0) {
          this.$router.push({name: 'index'})
          clearTimeout(this.time)
        }
      }, 1000)
    },
    methods: {
    },
    components: { }
  }
</script>



