<template>
  <section class="open-wrap">
      <nav class="open-control-nav">
        <p class="nav-left-title">全部服务分类</p>
        <template v-for="(item, index) in groupList">
          <a class="nav-left-list nav-left-active" @click="onClick(item.id, item.appname)" v-if="item.id == targetId">{{ item.appname }}</a>
          <a class="nav-left-list" @click="onClick(item.id, item.appname)" v-else>{{ item.appname }}</a>
        </template>
      </nav>
      <section class="open-wrap-content" v-if="isList">
        <el-table :data="apiList" border>
          <el-table-column label="API列表" prop="apichname"></el-table-column>
          <el-table-column label="API版本" prop="apiversion.version"></el-table-column>
          <el-table-column label="描述" prop="desc" show-overflow-tooltip>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <a class="open-list-btn" @click="openDetail(scope.row.id, scope.row.apichname)">查看</a>
              <a class="open-list-btn" @click="applyApi(scope.row.id, scope.$index)" v-if="userInfo && userInfo.userType == constans['SERVICE-USER'] && isCanApply(scope.row.isUseful)" >申请</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>
      <compontApiDetail :apiid="apiid" v-else></compontApiDetail>
    </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontApiDetail from '@/views/apiDetail.vue'
  import userService from '@/api/userService.js'
  import {getPageData} from '@/assets/js/common.js'
  export default{
    name: 'api-list-common',
    data () {
      return {
        applyId: this.$route.query.applyId || '',
        isList: true,
        targetId: '',
        apiid: '',
        currentPage: 1,
        pageSize: 10,
        groupList: [],
        apiList: [],
        allApiList: [],
        total: 0,
        userInfo: this.$store.state.login.userInfo,
        constans: constans
      }
    },
    created () {
      let pageSize = config.pageSize
      let currentPage = this.currentPage
      this.$ajax(config.url.groupList, {pageSize, currentPage}, config.method.groupList)
        .then((response) => {
          let oData = response.data
          let message = oData.message
          let nCode = oData.code
          if (nCode === '1') {
            let groupList = oData.data.items
            groupList.map(item=>{
              item.checkState == constans['STATE-PASS'] && this.groupList.push(item)
            })
            if (this.groupList) {
              this.targetId = this.groupList[0].id
              this.fetch(this.currentPage, this.targetId)
            }
          } else {
            this.$message.error(message)
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    methods: {
      // 是否可以申请
      isCanApply (isUseful) { // 有应用的时候才可以申请
        console.log('==========', isUseful, this.applyId)
        let bIsCanApply = isUseful && this.applyId ? true : false
        return bIsCanApply
      },
      //获取api列表
      fetch (currentPage, appid) {
        let pageSize = config.pageSize
        this.allApiList = []
        this.total = 0
        this.apiList = []
        let applyId = this.applyId
        this.$ajax(config.url.apiList, {currentPage, pageSize, appid, applyId}, config.method.apiList)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              let apiList = JSON.parse(oData.data).items
              apiList.map(item => {
                let checkState = item.checkState
                if ((checkState == constans['STATE-PASS'])) {
                  this.allApiList.push(item)
                  this.total++
                }
              })
              this.apiList = getPageData(this.pageSize, this.currentPage, this.allApiList)
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        // this.fetch(this.currentPage)
        this.apiList = getPageData(this.pageSize, this.currentPage, this.allApiList)
      },
      // 返回列表页
      onClick (id) {
        this.targetId = id
        this.isList = true
        this.$route.meta.curmbsData.length == 2 && this.$route.meta.curmbsData.pop()
        this.fetch(this.currentPage, this.targetId)
      },
      // 详情页
      openDetail (id, name) {
        this.apiid = id
        this.isList = false
        this.$route.meta.curmbsData.push({
          title: `${name}详情`,
          path: ''
        })
      },
      //申请审核api
      applyApi (apiId, nIndex) {
        let userId = this.userInfo.id
        let applyId = this.applyId
        userService.getResponse('generalSecretKey' ,{apiId, userId, applyId})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.$message.success('提交申请成功')
              this.visible = false
              let indexAll = nIndex + (this.currentPage - 1) * this.pageSize
              this.apiList[indexAll].isUseful = false
              this.apiList[nIndex].isUseful = false
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
    },
    components: { compontLeft, compontApiDetail }
  }
</script>



