<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <a class="open-group-add" @click="showPop">添加分类</a>
        <el-table :data="groupList" border>
          <el-table-column label="服务分类" prop="appname"></el-table-column>
          <el-table-column label="服务分类Key" prop="appkey"></el-table-column>
          <el-table-column label="创建者" prop="username"></el-table-column>
          <el-table-column label="状态" prop="checkState">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkState*1 == constans['STATE-NOPASS']">
                <p v-if="scope.row.checkMem">{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <template v-if="scope.row.checkState == constans['STATE-UNCOMMITTED'] && scope.row.userid == userid">
                <a class="open-list-btn" size="small" @click="submitCheck(scope.$index)">提交审核</a>
                <a class="open-list-btn" size="small" @click="showPop(scope.$index)">编辑</a>
                <a class="open-list-btn" @click="deleteGroup(scope.$index, scope.row.id)">删除</a>
              </template>
              <router-link class="open-list-btn" :to="{name: 'apiList', query: {appid: scope.row.id}}" v-if="scope.row.checkState==constans['STATE-PASS']">查看</router-link>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>

      <el-dialog :title="title" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
          <el-form-item label="分类名称" prop="appname" class="open-form-input">
            <el-input v-model="ruleForm.appname" placeholder="请输入分类名称"></el-input>
          </el-form-item>
          <el-form-item label="分类key" prop="appkey" class="open-form-input">
            <el-input v-model="ruleForm.appkey" placeholder="请输入分类key值"></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" @click="visible=false">取消</el-button>
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import {getPageData} from '@/assets/js/common.js'
  import compontLeft from '@/components/LEFT.vue'
  export default{
    name: 'group-list',
    data () {
      return {
        constans: constans, // 常量
        userid: this.$store.state.login.userInfo.id,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0, // 导航选中
        currentPage: 1,
        pageSize: 10,
        groupList: [],
        groupAllList: [], // 所有数据
        total: 0,
        ruleForm: {
          appname: '',
          appkey: ''
        },
        title: '添加分类',
        rules: {
          appname: [{
            required: true,
            message: '请输入分类名称',
            trigger: 'blur'
            }, {
            message: '分类名称只能包括中文、英文、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.appName
            }],
          appkey: [{
            required: true,
            message: '请输入分类key值',
            trigger: 'blur'
          }, {
            message: '分类key值只能包括英文、数字',
            trigger: 'change, blur',
            pattern: config.validate.apiEnName
          }]
        },
        visible: false,
        index: '',
        checkState: config.checkState
      }
    },
    created () {
      this.fetch(this.currentPage, this.userid)
    },
    methods: {
      formatterState (row) {
        return this.checkState[row.checkState]
      },
      //获取列表
      fetch (currentPage, userid) {
        let pageSize = config.pageSize
        this.groupAllList = []
        this.total = 0
        this.$ajax(config.url.groupList, {currentPage, pageSize}, config.method.groupList)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              let groupList = oData.data.items
              // this.total = oData.data.totalNumber
              groupList.map(item => {
                let userid = item.userid
                let checkState = item.checkState
                if ((this.userid != userid && checkState == constans['STATE-PASS']) || this.userid == userid) {
                  this.groupAllList.push(item)
                  this.total++
                }
              })
              this.groupList = getPageData(this.pageSize, this.currentPage, this.groupAllList)
              // this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.groupList = getPageData(this.pageSize, this.currentPage, this.groupAllList)
        // this.fetch(this.currentPage, this.userid)
      },
      // 删除
      deleteGroup (index, id) {
        this.$confirm('此操作将永久删除该服务分类, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$ajax(config.url.delgroup, {id}, config.method.delgroup)
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let nCode = oData.code
              if (nCode === '1') {
                this.fetch(this.currentPage, this.userid)
                /*this.groupList.splice(index, 1)
                // this.groupList = getPageData(this.pageSize, this.currentPage, this.groupAllList)
                this.total--*/

              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      },
      //打开弹窗
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        let targetData = this.groupList[nIndex]
        if (targetData) { // 修改
          this.title = '修改服务分类'
          this.ruleForm.appname = targetData.appname
          this.ruleForm.appkey = targetData.appkey
          this.ruleForm.id = targetData.id
        } else { // 添加
          this.title = '添加服务分类'
          this.ruleForm = {
            appname: '',
            appkey: ''
          }
        }
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            if (this.ruleForm.id) {
              this.modgroup()
            }else{
              this.addgroup()
            }
          } else {
            this.$message.error('保存失败!!');
            return false
          }
        })
      },
      //添加功能分类
      addgroup () {
        this.$ajax(config.url.addgroup, this.ruleForm, config.method.addgroup)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              /*this.ruleForm.id = oData.id
              this.ruleForm.checkState = 0
              this.ruleForm.userid = this.userid
              this.groupList.unshift(this.ruleForm)*/
              this.visible = false
              this.fetch(this.currentPage, this.userid)
              // this.groupList = getPageData(this.pageSize, this.currentPage, this.groupAllList)
              // this.total++
            } else {
              this.$message.error(message);
            }
          })
          .catch(error => {
            this.$message.error(error);
          })
      },
      //修改功能分类
      modgroup () {
        this.$ajax(config.url.modifygroup, this.ruleForm, config.method.modifygroup)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              // this.groupList[this.index].appname = this.ruleForm.appname
              let indexAll = this.index + (this.currentPage - 1) * this.pageSize
              this.groupAllList[indexAll].appname = this.ruleForm.appname
              this.groupAllList[indexAll].appkey = this.ruleForm.appkey
              this.groupList[this.index].appname = this.ruleForm.appname
              this.groupList[this.index].appkey = this.ruleForm.appkey
              this.visible = false
              this.$message.success('修改成功')
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      // 提交审核
      submitCheck (nIndex) {
        let targetData = this.groupList[nIndex]
        let id = targetData.id
        this.$ajax(config.url.auditGroup, {id}, config.method.auditGroup)
          .then(response => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              let indexAll = nIndex + (this.currentPage - 1) * this.pageSize
              this.groupAllList[indexAll].checkState = this.ruleForm.checkState
              targetData.checkState = 1
              // this.groupList[nIndex].checkState = 1
              this.$message.success('提交审核成功')
              console.log(this.groupList[nIndex])
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: { compontLeft }
  }
</script>



