<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content" v-if="isList">
        <el-table :data="apiList" border>
          <el-table-column label="API名称" prop="apiChName"></el-table-column>
          <el-table-column label="API版本" prop="version"></el-table-column>
          <el-table-column label="所属分类" prop="appName"></el-table-column>
          <el-table-column label="使用者" prop="userName">
            <!--<template scope="scope">
              <div v-for="value in scope.row.apiSecret" class="content-rowspan">{{ value.userName }}</div>
            </template>-->
          </el-table-column>
          <el-table-column label="使用的应用" prop="applyName"></el-table-column>
          <el-table-column label="申请时间" prop="applyTime" :formatter="formatter"></el-table-column>
          <el-table-column label="状态">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkMem && scope.row.checkState*1 == constans['STATE-NOPASS']">
                <p>{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <!--<div v-for="(value, index) in scope.row.apiSecret" class="content-rowspan">
                <a class="open-list-btn" size="small" @click="showPop(scope.$index, index)" v-if="value.checkState == constans['STATE-UNAUDITED']">审核</a>
              </div>-->
              <a class="open-list-btn" size="small" @click="showPop(scope.$index)" v-if="scope.row.checkState == constans['STATE-UNAUDITED']">审核</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>
      <el-dialog title="审核" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
          <el-form-item label="审核结果" class="open-form-input">
            <el-radio-group v-model="ruleForm.checkState">
              <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE-PASS'] || key==constans['STATE-NOPASS']">{{state}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="备注" prop="checkMem" class="open-form-input" v-if="ruleForm.checkState==constans['STATE-NOPASS']">
            <el-input v-model="ruleForm.checkMem" placeholder="请输入备注"></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')">提交</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontApiDetail from '@/views/apiDetail.vue'
  import userService from '@/api/userService.js'
  import {timeForatter} from '@/assets/js/dateTimeFormatter.js'
  export default{
    name: 'api-list-common',
    data () {
      return {
        constans: constans,
        userInfo: this.$store.state.login.userInfo,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 2,
        isList: true,
        visible: false,
        currentPage: 1,
        pageSize: 10,
        apiList: [],
        total: 0,
        checkState: config['auditCheckState'],
        index: 0,
        // apiSecretIndex: 0,
        ruleForm: {
          authId: '',
          checkState: '3',
          checkMem: ''
        },
        rules: {
          checkState: [{
            required: true,
            trigger: 'blur',
            message: '请选择审核状态'
          }],
          checkMem: [{
            required: true,
            trigger: 'blur',
            message: '请填写审核失败的原因'
          }]
        },
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize, this.userInfo.id)
    },
    methods: {
      formatter (row) {
        return timeForatter(row.applyTime)
      },
      //获取api列表
      fetch (currentPage, pageSize, userId) {
        this.$ajax(config.url.supplyList, {currentPage, pageSize, userId}, config.method.apiList)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.apiList = oData.data.items
              this.total = oData.data.totalNumber
              /*apiList.map(item => {
                item.apiSecret.length && this.apiList.push(item)
              })*/
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize, this.userInfo.id)
      },
      // 详情页
      openDetail (id, name) {
        this.apiid = id
        this.isList = false
        this.$route.meta.curmbsData.push({
          title: `${name}详情`,
          path: ''
        })
      },
      //打开弹窗
      showPop (nIndex, apiSecretIndex) {
        this.visible = true
        this.index = nIndex
        // this.apiSecretIndex = apiSecretIndex
        this.ruleForm.authId = this.apiList[nIndex].id
        this.ruleForm.checkState = '3'
        this.ruleForm.checkMem = ''
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.auditKey()
          } else {
            this.$message.error('保存失败!!')
            return false
          }
        })
      },
      //审核
      auditKey () {
        userService.getResponse('auditKey' ,this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            this.visible = false
            if (status === 'success') {
              this.$message.success('审核成功')
              this.apiList[this.index].checkState = this.ruleForm.checkState
              this.apiList[this.index].checkMem = this.ruleForm.checkMem
            } else {
              this.$message.error(message)
              this.fetch(this.currentPage, this.pageSize, this.userInfo.id)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
    },
    components: { compontLeft, compontApiDetail }
  }
</script>



