import * as types from '../mutation-types'
// import config from '@/assets/js/config.js'
// initial state
// shape: [{ id, quantity }]
const state = {
  // domain:'', // 保存后台请求的地址，修改时方便（比方说从测试服改成正式服域名）
  userInfo: null
}

// getters
const getters = {

}

// actions
const actions = {
}

// mutations
const mutations = {
  [types.UPDATEUSERINFO] (state, newUserInfo) {
    if (newUserInfo) {
      state.userInfo = { ...newUserInfo }
      let __time = new Date().getTime()
      localStorage.setItem('session', JSON.stringify({time: __time, userInfo: newUserInfo}))
    } else {
      state.userInfo = newUserInfo
      localStorage.removeItem('session')
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



