<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <a class="open-group-add" @click="showPop">添加应用</a>
      <el-table :data="applyList" border>
        <el-table-column label="应用名称" prop="applyName"></el-table-column>
        <el-table-column label="应用描述" prop="applyDesc" show-overflow-tooltip></el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
            <a class="open-list-btn" size="small" @click="showPop(scope.$index)">编辑</a>
            <a class="open-list-btn" @click="deleteApply(scope.row.id)">删除</a>
            <router-link class="open-list-btn" :to="{name: 'applyApiList', query: {applyId: scope.row.id}}">申请API</router-link>
            <router-link class="open-list-btn" :to="{name: 'useApiList', query: {applyId: scope.row.id}}">使用的API</router-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page"></el-pagination>
    </section>

    <el-dialog :title="title" :visible.sync="visible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <el-form-item label="应用名称" prop="applyName" class="open-form-input">
          <el-input v-model="ruleForm.applyName" placeholder="请输入应用名称"></el-input>
        </el-form-item>
        <el-form-item label="应用描述" prop="applyDesc" class="open-form-input">
          <el-input type="textarea" :row="3" v-model="ruleForm.applyDesc" placeholder="请输入应用描述"></el-input>
        </el-form-item>
        <el-form-item class="open-center">
          <el-button class="open-login-btn" @click="visible=false">取消</el-button>
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import {getPageData} from '@/assets/js/common.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  export default{
    name: 'Apply-list',
    data () {
      return {
        constans: constans, // 常量
        userId: this.$store.state.login.userInfo.id,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0, // 导航选中
        currentPage: 1,
        pageSize: 10,
        applyList: [],
        total: 0,
        ruleForm: {
          applyName: '',
          applyDesc: ''
        },
        title: '添加应用',
        rules: {
          applyName: [{
            required: true,
            message: '请输入应用名称',
            trigger: 'blur'
          }, {
            message: '应用名称只能包括中文、英文、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.appName
          }],
          applyDesc: [{
            required: true,
            message: '请输入应用描述',
            trigger: 'blur'
          }]
        },
        visible: false,
        index: 0
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize)
    },
    methods: {
      //获取列表
      fetch (currentPage, pageSize) {
        this.applyList = []
        let userId = this.userId
        userService.getResponseByBody('applyList', currentPage, pageSize, {userId})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.applyList = oData.data.items
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch( error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize)
      },
      // 删除应用
      deleteApply (id) {
        this.$confirm('此操作将永久删除该应用, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          userService.getResponse('delApply', {id})
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status === 'success') {
                this.fetch(this.currentPage, this.pageSize)
              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      },
      //打开弹窗
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        let targetData = this.applyList[nIndex]
        if (targetData) { // 修改
          this.title = '修改应用'
          this.ruleForm.applyName = targetData.applyName
          this.ruleForm.applyDesc = targetData.applyDesc
          this.ruleForm.id = targetData.id
        } else { // 添加
          this.title = '添加应用'
          this.ruleForm = {
            applyName: '',
            applyDesc: ''
          }
        }
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.addApply()
          } else {
            this.$message.error('保存失败!!');
            return false
          }
        })
      },
      //添加应用
      addApply () {
        userService.getResponse('addApply', this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message || '添加失败'
            let status = oData.status
            if (status === 'success') {
              this.visible = false
              if (this.ruleForm.id) { // 修改
                this.applyList[this.index].applyName = this.ruleForm.applyName
                this.applyList[this.index].applyDesc = this.ruleForm.applyDesc
                this.$message.success('修改成功')
              } else { // 添加
                this.fetch(this.currentPage, this.pageSize)
              }
            } else {
              this.$message.error(message);
            }
          })
          .catch(error => {
            this.$message.error(error);
          })
      }
    },
    components: { compontLeft }
  }
</script>



