<template>
  <section class="open-wrap">
    <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
    <section class="open-wrap-content">
      <a class="open-analysis-add" v-bind:class="{ openAnalysisAddActive: isActive==key }" @click="getAnalysis(key)" v-for="(time, key) in timeInterval">{{ time.value }}</a>
      <div class="open-echart" id="echartLine"></div>
      <div class="open-echart" id="echartPie"></div>
    </section>
  </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constants from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  import {getTime} from '@/assets/js/dateTimeFormatter.js'
  let echarts = require('echarts/lib/echarts')
  require('echarts/lib/component/title')
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/legend')
  require('echarts/lib/chart/line')
  require('echarts/lib/chart/pie')
  export default{
    name: 'data-analysis',
    data () {
      return {
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 0,
        isActive: 1,
        apiId: this.$route.query.apiId,
        timeInterval: config.timeInterval, // 时间间隔
        options: {
          'line': {
            title: { text: 'API请求数量和平均响应时间', left: 'left'},
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                animation: false
              }
            },
            legend: {
              data:['API请求数量','API平均响应时间'],
              x: 'right'
            },
            dataZoom: [
              {
                id: 'dataZoomX',
                type: 'slider',
                xAxisIndex: [0],
                filterMode: 'filter'
              },
              {
                id: 'dataZoomY',
                type: 'slider',
                yAxisIndex: [0],
                filterMode: 'empty'
              }
            ],
            xAxis: {
              type : 'category',
              boundaryGap : false,
              axisLine: {onZero: true},
              data: []
            },
            yAxis: [
              {
                name: '请求数量(个/分钟)',
                type: 'value'
              },
              {
                name: '平均响应时间（ms）',
                type: 'value'
              }
            ],
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            series: [{
              name:'API请求数量',
              type:'line',
              symbolSize: 8,
              hoverAnimation: false,
              data:[]
            }, {
              name:'API平均响应时间',
              type:'line',
              symbolSize: 8,
              yAxisIndex:1,
              hoverAnimation: false,
              data:[]
            }]
          },
          'pie': {
            title: { text: '状态码分布图', left: 'left' },
            tooltip : {
              trigger: 'item',
              formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            legend: {
              orient: 'vertical',
              top: 'center',
              left: 'right',
              data: []
            },
            series : [
              {
                name:'访问来源',
                type: 'pie',
                radius : '65%',
                center: ['30%', '50%'],
                selectedMode: 'single',
                data:[]
              }
            ]
          }
        }
      }
    },
    created () {
      this.getAnalysis(1)
    },
    computed: {
    },
    methods: {
      getAnalysis (type) {
        this.isActive = type
        let apiKey = this.apiId
        let dateTime = new Date().getTime()
        let param = this.$store.state.login.userInfo.userType
        this.options.line.xAxis.data = []
        this.options.line.series[0].data = []
        this.options.line.series[1].data = []
        this.options.pie.legend.data = []
        this.options.pie.series[0].data = []

        this.getResponse(param, dateTime, apiKey, type)
      },
      getResponse(param, dateTime, apiKey, type) {
        let timeInterval = this.timeInterval[type].time
        userService.getResponse('statisticsLineChartAndPie', {param, dateTime, apiKey, type})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let code = oData.code
            if (code === '1') {
              let lineOption = oData.data.bucketsLine
              let pieOption = oData.data.bucketsPie
              lineOption.map(item=>{ // 折线图
                this.options.line.xAxis.data.push(getTime(item.keyAsString)) // getTimeByMin(dateTime, timeInterval)
                this.options.line.series[0].data.push(item.doccount)
                this.options.line.series[1].data.push(item.intavg)
              })
              this.creatChart('#echartLine', 'line')
              pieOption.map(item=>{ // 饼图
                this.options.pie.legend.data.push(item.key)
                this.options.pie.series[0].data.push({
                  value:item.value,name: item.key
                })
              })
              !this.options.pie.legend.data.length && this.options.pie.legend.data.push(400) && this.options.pie.series[0].data.push({value: 0, name: 400})
              this.creatChart('#echartPie', 'pie')
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      creatChart (queryString, type) {
        let myChart = echarts.init(document.querySelector(queryString))
        myChart.setOption( this.options[type])
      },
    },
    components: { compontLeft }
  }
</script>



