    data () {
      return {
        curmbsData: this.$route.meta.curmbsData, // this.$route.meta.curmbsData,
        userInfo: this.$store.state.login.userInfo
        // userInfo: []
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      // 刷新
      // this.checkLogin()
    },
    methods: {
      fetchData () {
        // 路由改变
        this.curmbsData = this.$route.meta.curmbsData
      },
      checkLogin () {
        if (!this.userInfo) {
          let userInfo = sessionStorage.getItem('session')
          userInfo && this.$store.commit(types.UPDATEUSERINFO, JSON.parse(userInfo))
          /* this.$ajax(config.url.checklogin, {}, config.method.checklogin).then(response => {
            let oData = response.data
            let nCode = oData.code
            let userInfo = oData.data
            if (nCode === '1') {
              this.$store.commit(types.UPDATEUSERINFO, userInfo)
              this.userInfo = this.$store.state.login.userInfo
            } else {
              this.$router.push({
                name: 'login'
              })
            }
          }) */
        }
        // this.userInfo = this.$store.state.login.userInfo
      }
    }
  }
</script>

<style lang = "less">
  #app{
    width: 100%;
    height: 100%;
  }
</style>



