const config = {
  login: {
    url: '/user/login',
    method: 'POST'
  },
  logout: {
    url: '/user/logout',
    method: 'POST'
  },
  register: {
    url: '/user/register',
    method: 'POST'
  },
  checklogin: {
    url: '/user/checklogin',
    method: 'POST'
  },
  checkName: {
    url: '/user/checkUserName',
    method: 'GET'
  },
  checkEmail: {
    url: '/user/checkEmail',
    method: 'GET'
  },
  auditApi: {
    url: '/admin/checkApi',
    method: 'POST'
  },
  checkVersion: { // 校验api版本
    url: 'apis/checkVersion',
    method: 'GET'
  },
  checkApiEnName: { // 校验api英文名
    url: 'apis/checkApiEnName',
    method: 'GET'
  },
  checkApiChName: { // 校验api中文名
    url: 'apis/checkApiChName',
    method: 'POST'
  },
  generalSecretKey: { // 生成密钥
    // url: '/apis/generalSecretKey',
    url: 'api/applyApi',
    method: 'POST'
  },
  delKey: { // 删除密钥
    url: 'api/deleteAuditEntity',
    method: 'GET'
  },
  'auditKey': { // 密钥审核
    // url: '/key/auditKey',
    url: 'api/allowApi',
    method: 'POST'
  },
  applyList: { // 使用者应用列表
    url: 'apply/findApplyList',
    method: 'POSTJSON'
  },
  addApply: { // 添加应用
    url: 'apply/saveApply',
    method: 'POSTJSON'
  },
  delApply: { // 删除应用
    url: 'apply/deleteApplyByid',
    method: 'DELETE'
  },
  useApiList: { // 获取单个应用下的信息
    url: 'apply/getApplyByid',
    method: 'GET'
  },
  statisticsLineChartAndPie: { // api数据分析
    url: 'statistics/statisticsLineChartAndPie',
    method: 'POST'
  },
  modifyApi: { // 修改api
    url: '/apis/modifyApi',
    method: 'POSTJSON'
  }
}
export default config



