<template>
  <section class="open-login-bg">
    <div class="open-login-wrap">
      <div class="open-login-img"></div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <h3 class="open-content-title">登录</h3>
        <el-form-item label="用户名" prop="username" class="open-form-input">
          <el-input v-model="ruleForm.username" placeholder="请输入用户名" @keyup.enter.native="onEnter(ruleForm)"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" class="open-form-input">
          <el-input type="password" v-model="ruleForm.password" placeholder="请输入密码" @keyup.enter.native="onEnter(ruleForm)"></el-input>
        </el-form-item>
        <el-form-item class="open-center">
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" autofocus>登录</el-button>
        </el-form-item>
        <p class="open-message">还没有账号，先去<router-link :to="{name: 'signUp'}">注册</router-link></p>
      </el-form>
    </div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import * as types from '@/store/mutation-types.js'
  import userService from '@/api/userService.js'
  import md5 from 'js-md5'
  export default{
    name: 'login',
    data(){
      return {
        ruleForm: {
          username: '',
          password: ''
        },
        rules: {
          username: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur',
          }, {
            message: '用户名只能包括数字、英文、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.userName
          }],
          password: [{
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          }, {
            message: '密码只能包括数字、字母、.、-、*、@、?、!',
            trigger: 'change, blur',
            pattern: config.validate.passWord
          }]
        }
      }
    },
    methods: {
      // 点击登录
      submitForm (ruleForm) {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.login()
          } else {
            this.$message.error('登录失败!!');
            return false
          }
        })
      },
      // 登录
      login () {
        this.ruleForm.password = md5(this.ruleForm.password);
        userService.getResponse('login', this.ruleForm).then(response => {
          let oData = response.data
          let sMessage = oData.message;
          let nCode = oData.code;
          let userInfo = oData.data;
          if (nCode === '1') {
            this.$store.commit(types.UPDATEUSERINFO, userInfo)
            this.$router.push({
              name: 'index'
            })
          }else{
            this.$message.error(sMessage);
          }
        })
      },
      // enter键
      onEnter() {
        this.$nextTick(function() {
          this.submitForm ()
        })
      }
    },
    components: { }
  }
</script>



