<template>
    <header class="open-header">
      <section class="open-header-wrap">
        <a href="#" class="open-header-logo"></a>
        <nav class="open-header-nav" v-for="nav in navs">
            <router-link :to="{ name: nav.pathName }">{{nav.name}}</router-link>
        </nav>
        <template v-if="userInfo && userInfo.username">
          <div class="open-header-person">
            {{ userTypeName }}/{{ userInfo.username }}
            <nav class="open-header-person-opear">
              <router-link :to="{ name: person }">个人中心</router-link>
              <a @click="logout">退出</a>
            </nav>
          </div>
        </template>
        <template v-else>
          <div class="open-header-login">
            <router-link :to="{name: 'login'}">登录</router-link>
            <router-link :to="{name: 'signUp'}">注册</router-link>
          </div>
        </template>
        <!--<div class="open-header-search">
          <input class="open-header-input" type="text" placeholder="帮助文档/API搜索"/>
          <span class="open-header-btn"></span>
        </div>-->
      </section>
    </header>
</template>

<script type='es6'>
  import { mapGetters } from 'vuex'
  import constans from '@/assets/js/constans.js'
  import * as types from '@/store/mutation-types.js'
  import config from '@/assets/js/config.js'
  import userService from '@/api/userService.js'
  export default{
    name: 'custom-header',
    data(){
        return{
          person: '',
          navs: [{
            name: 'API文档',
            pathName: 'apiListCommon'
          }, {
            name: '控制台',
            pathName: ''
          }, {
            name: '帮助中心',
            pathName: 'newGuide'
          }],
          userInfo: {}
        }
    },
    watch: {
      '$route': 'creatUserInfo'
    },
    created () {
      this.creatUserInfo()
    },
    // props: ['userInfo'],
    /* computed: {
      userTypeName () {
        if (!this.userInfo.id) {
          this.navs[1].path = '/login'
        } else {
          return config.userType[this.userInfo.userType]
        }
      }
    }, */
    methods: {
      creatUserInfo () {
        this.userInfo = this.$store.state.login.userInfo

        if (!this.userInfo) {
          this.navs[1].pathName = 'login'
        } else {
          this.userTypeName = config.userType[this.userInfo.userType]
          this.person = this.userInfo.userType == constans['SERVICE-USER'] ? 'developPerson' : 'supplyPerson'
          this.navs[1].pathName = this.userInfo.userType == constans['SERVICE-USER'] ? 'applyList' : 'groupList'
        }
      },
      logout () {
        userService.getResponse('logout', {})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.$message.success('退出登录')
              this.$store.commit(types.UPDATEUSERINFO, null)
              this.$router.push({
                path: '/'
              })
              this.userInfo = config.userInfo
            } else {
              console.log(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: {

    }
  }
</script>



