<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <article class="open-content-list">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
            <el-form-item label="服务分类" prop="appid">
              <el-select v-model="ruleForm.appid" placeholder="请输入所属功能属性" :disabled="version">
                <el-option v-for="group in groups" :key="group.id" :label="group.name" :value="group.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="API名称" prop="apichname">
              <el-input v-model.trim="ruleForm.apichname" placeholder="请输入API名称" :disabled="version"></el-input>
            </el-form-item>
            <el-form-item label="API key" prop="apienname">
              <el-input v-model="ruleForm.apienname" placeholder="请输入API key" :disabled="version"></el-input>
            </el-form-item>
            <el-form-item label="调用方法" prop="httpmethod">
              <el-select v-model="ruleForm.httpmethod" :disabled="version">
                <el-option v-for="httpmd in httpmds" :label="httpmd" :value="httpmd"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="状态" prop="state">
              <el-select v-model="ruleForm.state" :disabled="version">
                <el-option :label="state" :value="key" v-for="(state, key) in states"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="正式URL" prop="testendpoint">
              <el-input v-model="ruleForm.testendpoint" placeholder="请输入URL地址"></el-input>
            </el-form-item>
            <!--<el-form-item label="正式URL" prop="endpoint">
              <el-input v-model="ruleForm.endpoint" plfaceholder="请输入正式URL"></el-input>
            </el-form-item>-->
            <el-form-item label="接口描述" prop="desc">
              <el-input type="textarea" :rows="3" v-model="ruleForm.desc" placeholder="请输入接口描述，不超过500字符" :disabled="version"></el-input>
            </el-form-item>
            <el-form-item label="Header参数" class="is-required">
              <a class="open-add" @click="handelAddTable(0)">+ 新增</a>
              <table cellpadding="0" cellspacing="0" class="open-wrap-table">
                <tr class="open-table-head">
                  <th>参数名称</th>
                  <th>参数类型</th>
                  <th>可否为空</th>
                  <th>示例</th>
                  <th>描述</th>
                  <th class="open-widtn-30">操作</th>
                </tr>
                <tr class="open-table-list" v-if="ruleForm.headers" v-for="(item, index) in ruleForm.headers">
                  <td><el-form-item :prop="'headers[' + index + '].argname'" :rules="rules.argname"><el-input v-model="item.argname" placeholder="a"></el-input></el-form-item></td>
                  <td>
                    <el-select v-model="item.argtype">
                      <el-option v-for="argType in argTypes" :label="argType" :value="argType"></el-option>
                    </el-select>
                  </td>
                  <td>
                    <el-select v-model="item.required">
                      <el-option :label="name" :value="key" v-for="(name, key) in requireds"></el-option>
                    </el-select>
                  </td>
                  <td><el-input v-model="item.example"></el-input></td>
                  <td><el-input v-model="item.desc"></el-input></td>
                  <td>
                    <template v-if="index==0">
                      —
                    </template>
                    <template v-else>
                      <a class="open-table-btn open-table-width" @click="deleteDoc(0, index)">删除</a>
                    </template>
                  </td>
                </tr>
              </table>
            </el-form-item>
            <el-form-item label="应用参数" class="is-required">
              <a class="open-add" @click="handelAddTable(1)">+ 新增</a>
              <table cellpadding="0" cellspacing="0" class="open-wrap-table">
                <tr class="open-table-head">
                  <th>参数名称</th>
                  <th>参数类型</th>
                  <th>可否为空</th>
                  <th>示例</th>
                  <th>描述</th>
                  <th class="open-widtn-30">操作</th>
                </tr>
                <tr class="open-table-list" v-if="ruleForm.args" v-for="(item, index) in ruleForm.args">
                  <td>
                    <el-form-item :prop="'args[' + index + '].argname'" :rules="rules.argname">
                      <el-input v-model="item.argname" placeholder="a"></el-input>
                    </el-form-item>
                  </td>
                  <td>
                    <el-select v-model="item.argtype">
                      <el-option v-for="argType in argTypes" :label="argType" :value="argType"></el-option>
                    </el-select>
                  </td>
                  <td>
                    <el-select v-model="item.required">
                      <el-option v-for="(key, value) in requireds" :label="key" :value="value"></el-option>
                    </el-select>
                  </td>
                  <td><el-form-item><el-input v-model="item.example"></el-input></el-form-item></td>
                  <td><el-form-item><el-input v-model="item.desc"></el-input></el-form-item></td>
                  <td>
                    <template v-if="index==0">
                      —
                    </template>
                    <template v-else>
                      <a class="open-table-btn open-table-width" @click="deleteDoc(1, index)">删除</a>
                    </template>
                  </td>
                </tr>
              </table>
            </el-form-item>
            <el-form-item label="返回信息" class="is-required">
              <a class="open-add" @click="handelAddTable(2)">+ 新增</a>
              <table cellpadding="0" cellspacing="0" class="open-wrap-table">
                <tr class="open-table-head">
                  <th>参数名称</th>
                  <th>参数类型</th>
                  <th>示例值</th>
                  <th>描述</th>
                  <th class="open-widtn-30">操作</th>
                </tr>
                <tr class="open-table-list" v-if="ruleForm.result" v-for="(item, index) in ruleForm.result">
                  <td><el-form-item :prop="'result[' + index + '].retname'" :rules="rules.argname"><el-input v-model="item.retname" placeholder="a"></el-input></el-form-item></td>
                  <td>
                    <el-select v-model="item.rettype">
                      <el-option v-for="argType in argTypes" :label="argType" :value="argType"></el-option>
                    </el-select>
                  </td>
                  <td><el-form-item><el-input v-model="item.example"></el-input></el-form-item></td>
                  <td><el-form-item><el-input v-model="item.desc"></el-input></el-form-item></td>
                  <td>
                    <template v-if="index==0">
                      —
                    </template>
                    <template v-else>
                      <a class="open-table-btn open-table-width" @click="deleteDoc(2, index)">删除</a>
                    </template>
                  </td>
                </tr>
              </table>
            </el-form-item>
            <el-form-item label="返回示例" class="is-required">
              <template>
                <table cellpadding="0" cellspacing="0" class="open-wrap-table">
                  <!--<tr class="open-table-list">
                    <td class="open-example-title">示例值名称</td>
                    <td class="open-example-content"><el-form-item :prop="'retexample.exname'" :rules="rules.argname"><el-input v-model="ruleForm.retexample.exname" placeholder="请输入示例值名称"></el-input></el-form-item></td>
                  </tr>-->
                  <tr class="open-table-list">
                    <td class="open-example-title">示例值类型</td>
                    <td class="open-example-content">
                      <el-select v-model="ruleForm.retexample.extype">
                        <el-option v-for="reasonType in reasonTypes" :label="reasonType" :value="reasonType"></el-option>
                      </el-select>
                    </td>
                  </tr>
                  <tr class="open-table-list">
                    <td class="open-example-title">状态码</td>
                    <td class="open-example-content"><el-form-item :prop="'retexample.statecode'" :rules="rules.statecode"><el-input v-model="ruleForm.retexample.statecode"></el-input></el-form-item></td>
                  </tr>
                  <tr class="open-table-list">
                    <td class="open-example-title">示例值</td>
                    <td class="open-example-content"><el-form-item :prop="'retexample.exvalue'" :rules="rules.exvalue"><el-input type="textarea" :rows="5" v-model="ruleForm.retexample.exvalue" placeholder="请输入示例值"></el-input></el-form-item></td>
                  </tr>
                </table>
              </template>
            </el-form-item>
            <el-form-item label="错误信息" prop="errcodes" class="is-required">
              <a class="open-add" @click="handelAddTable(3)">+ 新增</a>
              <table cellpadding="0" cellspacing="0" class="open-wrap-table">
                <tr class="open-table-head">
                  <th>错误代码</th>
                  <th>描述</th>
                  <th>操作</th>
                </tr>
                <tr class="open-table-list" v-if="ruleForm.errcodes" v-for="(item, index) in ruleForm.errcodes">
                  <td><el-form-item :prop="'errcodes[' + index + '].errname'" :rules="rules.errname"><el-input v-model="item.errname" placeholder="a"></el-input></el-form-item></td>
                  <td><el-form-item><el-input v-model="item.desc"></el-input></el-form-item></td>
                  <td>
                    <template v-if="index==0">
                      —
                    </template>
                    <template v-else>
                      <a class="open-table-btn open-table-width" @click="deleteDoc(3, index)">删除</a>
                    </template>
                  </td>
                </tr>
              </table>
            </el-form-item>
            <el-form-item label="频次" prop="rate">
              <el-input v-model="ruleForm.rate" placeholder="请输入频次，最大值不要超过100">
                <template slot="append">次/秒</template>
              </el-input>
            </el-form-item>
            <!--<el-form-item label="频次周期">
              <el-input value="1s" :disabled="true"></el-input>
            </el-form-item>-->
            <el-form-item label="最大配额" prop="quota_max">
              <el-input v-model="ruleForm.quota_max" placeholder="请输入最大配额"></el-input>
            </el-form-item>
            <el-form-item label="配额重置周期" prop="quota_renewal_rate">
              <el-select v-model="ruleForm.quota_renewal_rate" placeholder="请选择配额重置周期">
                <el-option v-for="(value, key) in gwRate['quotaRenewalRate']" :key="key * 1" :label="value" :value="key * 1"></el-option>
              </el-select>
            </el-form-item>
            <!--<el-form-item label="版本控制" v-if="!version">
              <el-radio-group v-model="isOpenVersion">
                <el-radio class="radio" :label="true">开启</el-radio>
                <el-radio class="radio" :label="false">不开启</el-radio>
              </el-radio-group>
            </el-form-item>-->
            <template>
              <el-form-item label="版本号" :prop="'apiversion.version'" :rules="rules.version">
                <el-input v-model="ruleForm.apiversion.version"></el-input>
              </el-form-item>
              <el-form-item label="版本描述" :prop="'apiversion.versionRemark'" :rules="rules.versionRemark">
                <el-input type="textarea" :rows="3" v-model="ruleForm.apiversion.versionRemark" placeholder="请输入版本描述，不超过500字符"></el-input>
              </el-form-item>
            </template>
            <el-form-item>
              <el-button class="open-login-btn" @click="$router.push({name: 'apiList', query: {appid: ruleForm.appid}})">取消</el-button>
              <el-button type="primary" @click="onSubmit('ruleForm', 0)">保存</el-button>
              <el-button @click="onSubmit('ruleForm', 1)">提交审核</el-button>
            </el-form-item>
          </el-form>
        </article>
      </section>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'
  import * as types from '@/store/mutation-types.js'
  export default{
    name: 'add-api',
    data () {
      var validateArgName = (rule, value, callback) => {
        let ruleFiled = rule.field
        let ruleFName = ruleFiled.match(/(\S*)\[/g)
        if (!ruleFName) {
          callback();
          return
        }
        ruleFName = ruleFName[0].substring(0, ruleFName[0].length - 1)
        let ruleCName = ruleFiled.match(/\.(\S*)/g)[0]
        ruleCName = ruleCName.substring(1, ruleCName.length)
        let index = ruleFiled.match(/\d/g)[0]*1
        let aRuleData = this.ruleForm[ruleFName]
        aRuleData && aRuleData.map((item, nIndex) => {
          if (index !== nIndex && value === item[ruleCName]) {
            callback(new Error('参数重复'));
          }
        })
        callback();
      };
      let validateRate = (rule, value, callback) => {
        value <= this.gwRate.maxRate ? callback() : callback(new Error('频次不能超过100'))
      }
      let validateQuotaMax = (rule, value, callback) => {
        value <= this.gwRate.quotaMax ? value * 1 > this.ruleForm.rate ? callback() : callback(new Error('最大配额应该大于频次')) : callback(new Error('最大配额不能超过5000'))
      }
      let validateVersion = (rule, value, callback) => {
        let apiversion = this.ruleForm.apiversion
        console.log('=====', apiversion.apiId, this.version, value == this.original.version)
        if (!apiversion.apiId || (!this.version && value == this.original.version)) { // 第一次添加版本不校验 // 修改版本号不跟自身校验
          callback()
        } else {
          userService.getResponse('checkVersion', {apiId: apiversion.apiId, version: value})
            .then(response => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status != 'success') {
                callback(new Error(message));
              } else {
                callback()
              }
            })
            .catch((error) => {
              callback(new Error('接口错误，请联系管理员'));
            })
        }
      }
      let validateEnName = (rule, value, callback) => {
        let appId = this.ruleForm.appid
        if (this.version || value == this.original.apiEnName) { // 更新版本 不校验英文名 //修改英文名不跟自身校验
          callback()
        } else {
          userService.getResponse('checkApiEnName', {apiEnName: value, appId: appId})
            .then(response => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status != 'success') {
                callback(new Error(message));
              } else {
                callback()
              }
            })
            .catch((error) => {
              callback(new Error('接口错误，请联系管理员'));
            })
        }
      }
      let validateChName = (rule, value, callback) => {
        let appId = this.ruleForm.appid
        if (this.version || value == this.original.apiChName) { // 更新版本 不校验英文名 //修改英文名不跟自身校验
          callback()
        } else {
          userService.getResponse('checkApiChName', {apiChName: value, appId: appId})
            .then(response => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status != 'success') {
                callback(new Error(message));
              } else {
                callback()
              }
            })
            .catch((error) => {
              callback(new Error('接口错误，请联系管理员'));
            })
        }
      }
      return {
        userid: this.$store.state.login.userInfo.id,
        navData: config.navLeft[this.$store.state.login.userInfo.userType],
        targetName: 1,
        appid: this.$route.query.appid,
        apiid: this.$route.query.apiid,
        version: this.$route.query.version ? true : false,
        isOpenVersion: false,
        states: config.openState,
        ruleForm: {
          apichname: '', // API名称
          apienname: '', // API key
          appid: this.$route.query.appid, // 所属功能组ID
          httpmethod: 'POST', // 请求类型(GET或POST)
          testendpoint: '', // 测试地址
          // endpoint: '', // 正式地址
          desc: '', // 描述
          state: '1', // 状态
          headers: [{'argname': '', 'argtype': 'string', 'required': 'true', 'example': '', 'desc': ''}],
          args: [{"argname":"","argtype":"string","required":'true',"example":"","desc":""}],
          result: [{"retname":"","rettype":"string","example":"","desc":""}],
          errcodes: [{"errname":"","desc":""}],
          retexample: {"extype":"json","statecode":"","exvalue":""},
          rate: 1, // 频次
          per: 1, // 周期1s
          quota_max: 100, // 最大配额
          quota_renewal_rate: 3600000, // 配额重置周期
          apiversion: {
            'version': '',
            'versionRemark': ''
          }
        },
        rules: {
          apichname: [{
            required: true,
            message: '请输入API名称',
            trigger: 'change, blur'
          }, {
            trigger: 'change, blur',
            pattern: config.validate.apiChName,
            message: 'API名称只能输入中文、英文、数字、.、_'
          }, {
            validator: validateChName,
            trigger: 'blur'
          }],
          apienname: [{
            required: true,
            message: '请输入API key',
            trigger: 'blur, change'
          }, {
            trigger: 'blur, change',
            pattern: config.validate.apiEnName,
            message: 'API key只能输入英文、数字'
          }, {
              validator: validateEnName,
              trigger: 'blur'
            }],
          desc: [{
            required: true,
            message: '请输入接口描述',
            trigger: 'blur'
          }],
          appid: [{
            required: true,
            message: '请选择功能组',
            trigger: 'blur'
          }],
          httpmethod: [{
            required: true,
            message: '请选择请求类型',
            trigger: 'blur'
          }],
          state: [{
            required: true,
            message: '请选择状态',
            trigger: 'blur'
          }],
          testendpoint: [{
            required: true,
            message: '请输入测试地址',
            trigger: 'blur',
            type: 'url'
          }],
          /* endpoint: [{
            required: true,
            message: '请输入正式地址',
            trigger: 'blur',
            type: 'url'
          }], */
          errname: [{
            required: true,
            message: '请添加错误码',
            trigger: 'change, blur',
          }, {
            trigger: 'change, blur',
            pattern: config.validate.errname,
            message: '错误码只能输入英文，数字，-，_',
          }, {
            validator: validateArgName,
            trigger: 'blur'
          }],
          statecode: [{
            required: true,
            message: '请添加示例状态码',
            trigger: 'change, blur',
            pattern: config.validate.number
          }],
          argname: [{
            required: true,
            message: '请输入参数名称',
            trigger: 'blur'
          }, {
            message: '参数名称包含英文、数字、$、-和_，第一个字符不允许是数字',
            trigger: 'change, blur',
            pattern: config.validate.paramName
          }, {
            validator: validateArgName,
            trigger: 'blur'
          }],
          exvalue: {
            required: true,
            message: '请添加示例值',
            trigger: 'blur, change'
          },
          headers: {
            required: true,
            message: '请添加请求头',
            trigger: 'blur'
          },
          rate: [{
            message: '格式错误',
            trigger: 'change, blur',
            pattern: config.validate.number
          }, {
            required: true,
            validator: validateRate,
            trigger: 'blur'
          }],
          /*per: [{
           required: true,
           message: '请输入频次周期'
           }, {
           message: '格式错误',
           type: config.validate.number
           }],*/
          quota_max: [{
            required: true,
            message: '格式错误',
            trigger: 'change, blur',
            pattern: config.validate.number
          }, {
            validator: validateQuotaMax,
            trigger: 'blur'
          }],
          quota_renewal_rate: [{
            required: true,
            message: '请输入配额重置周期'
          }],
          version: [{
            required: true,
            message: '请输入版本号',
            trigger: 'blur'
          }, {
            message: '版本号只能包含英文、数字',
            pattern: config.validate.apiEnName,
            trigger: 'change, blur'
          }, {
            validator: validateVersion,
            trigger: 'blur'
          }],
          versionRemark: [{
              required: true,
              message: '请输入版本描述',
              trigger: 'blur'
            }
          ]
        },
        groups: [],
        argTypes: config.argTypes,
        reasonTypes: config.reasonTypes,
        requireds: config.requireds,
        httpmds: config.httpmds,
        addType: {
          0: 'headers',
          1: 'args',
          2: 'result',
          3: 'errcodes'
        },
        initStruct: {
          headers: {
            argname: '',
            argtype: 'string',
            required: 'true',
            example: '',
            desc: ''
          },
          args: {
            argname: '',
            argtype: 'string',
            required: 'true',
            example: '',
            desc: ''
          },
          result: {
            retname: '',
            rettype: 'string',
            example: '',
            desc: ''
          },
          errcodes: {
            errname: '',
            desc: ''
          }
        },
        gwRate: config.gwRate, // 网关限频
        original: { // 原始数据
          apiEnName: '',
          apiChName: '',
          version: ''
        }
      }
    },
    created () {
      if (this.apiid) {
        this.$ajax(config.url.showapidetail, {apiid: this.apiid}, config.method.showapidetail)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              oData.data.state = oData.data.state.toString()
              this.ruleForm = oData.data
              this.original.apiEnName = this.ruleForm.apienname
              this.original.apiChName = this.ruleForm.apichname
              this.original.version = this.ruleForm.apiversion.version
              // this.formatterVersion(this.ruleForm)
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
      let pageSize = config.pageSize
      let currentPage = 1
      // 获取功能组
      this.$ajax(config.url.groupList, {pageSize: pageSize, currentPage: currentPage}, config.method.groupList)
        .then((response) => {
          let oData = response.data
          let message = oData.message
          let nCode = oData.code
          if (nCode === '1') {
          let dataList = oData.data
          let allData = dataList.items
          allData.forEach((item) => {
            let id = item.id
            let name = item.appname
            let checkState = item.checkState
            checkState==2 && this.groups.push({
            id: id,
            name: name
          })
        })
        } else {
            this.$message.error(message)
        }
      })
      .catch(error => {
      console.log(error)
      })
    },
    methods: {
      formatterVersion (row) {
        row.apiversion.version = row.apiversion.version + 1
      },
       jsonToString (data, nType) {
        // let newData = {}
        /* for (let key in data) {
          let value = data[key]
          newData[key] = typeof value === "object" ? JSON.stringify(value) : value
        } */
         data.checkState = nType
        return data
      },

      // 提交
      onSubmit (ruleForm, nType) {
        this.$refs['ruleForm'].validate((valid) => {
          console.log('valid', valid)
          if (valid) {
            this.ruleForm.id && !this.version ? this.modifyApi(nType) : this.submitData(nType)
          } else {
            this.$message.error('添加失败')
        return false
      }
      })
      },
      // 保存接口
      submitData (nType) {
        this.apiid && this.version && delete this.ruleForm.id
        this.$ajax(config.url.addApi, this.jsonToString(this.ruleForm, nType), config.method.addApi).then(response => {
          let oData = response.data
          let message = oData.message
          let nCode = oData.code
          let msg = '恭喜您，添加成功'
          if (nCode * 1 === 1) {
            this.$message.success(msg)
            this.$router.push({
              name: 'apiList',
              query: {
                appid: this.ruleForm.appid
              }
            })
          } else if (nCode * 1 === 0) {
            this.$message.error(message)
            this.$router.push({
              name: 'login'
            })
            this.$store.commit(types.UPDATEUSERINFO, null)
          } else {
            this.$message.error(message)
          }
        }).catch(error => {
          this.$message.error('添加失败')
        })
      },
      // 修改接口
      modifyApi (nType) {
        userService.getResponse('modifyApi', this.jsonToString(this.ruleForm, nType)).then(response => {
          let oData = response.data
          let message = oData.message
          let msg = '恭喜您，修改成功'
          let status = oData.status
          if (status == 'success') {
            this.$message.success(msg)
            this.$router.push({
              name: 'apiList',
              query: {
                appid: this.ruleForm.appid
              }
            })
          } else {
            this.$message.error(message)
          }
        }).catch(error => {
          this.$message.error('修改失败')
        })
      },
      // 点击新增
      handelAddTable (type) {
        let key = this.addType[type]
        let originalData = this.initStruct[key]
        this.ruleForm[key].push(Object.assign({}, originalData))
      },
      // 点击删除
      deleteDoc (type, nIndex) {
        let key = this.addType[type]
        this.ruleForm[key].splice(nIndex, 1)
      }
    },
    components: { compontLeft }
  }
</script>



